import type { CodePluginConfig } from './types';
import lazyRenderStrategy from '../common/queries/lazyRenderStrategy';
import createOnAction from './handlers/createOnAction';
import createRenderBlock from './mobile/createRenderBlock';
import createOnKeyDown from './handlers/createOnKeyDown';
import CodeModel from '../mo/models';
import moFactory from '../mo/index';
import type { MobileBiFactory } from '@ali/4ever-cangjie';

export const mobileBiFactory: MobileBiFactory = (configs) => {
  const codeConfig = configs.code || {} as CodePluginConfig;

  return {
    ...moFactory({}),
    queries: {
      lazyRenderStrategy,
    },
    renderNode: createRenderBlock(codeConfig),
    onKeyDown: createOnKeyDown(codeConfig, false),
    onAction: createOnAction({} as CodePluginConfig),
    locale: codeConfig.locale,
    models: {
      klass: 'block',
      type: 'code',
    },
  };
};
